import {
  SummaryGroup,
  SummaryTitleContainer,
  SummaryContainer,
  TitleDivider,
  SummaryField,
  SummaryValue,
  SummaryTitle,
} from './PerformanceSummary.styles';

interface PerformanceSummaryElement {
  field: string;
  value: string | number;
}

interface PerformanceSummaryProps {
  data: PerformanceSummaryElement[];
  performanceSummaryTitle: string;
}
export const PerformanceSummary = ({
  data,
  performanceSummaryTitle,
}: PerformanceSummaryProps) => {
  return (
    <section>
      <SummaryGroup>
        <SummaryTitleContainer>
          <SummaryTitle>{performanceSummaryTitle}</SummaryTitle>
        </SummaryTitleContainer>
        <SummaryContainer>
          {data.map((dataElement: PerformanceSummaryElement, index: number) => (
            <div key={index}>
              <SummaryField>{dataElement.field}</SummaryField>
              <SummaryValue>{dataElement.value}</SummaryValue>
            </div>
          ))}
        </SummaryContainer>
      </SummaryGroup>
    </section>
  );
};
